<template>
  <div class="home">
    <!-- banner -->
    <div class="banner">
      <div class="container">
        <div class="row">
          <div class="banner_l col-lg-6 col-sm-12">
            <div class="banner_l_card">
              <Swiper :showData="showData" :siteData="siteData" :place="place"/>
            </div>
          </div>
          <div class="banner_r col-lg-6 col-sm-12">
            <div class="banner_r_card">
              <p>
                <span>{{nowYear}}</span>
              </p>
              <p>
                <span>{{nowDay}}.{{nowMounth}}/{{nowDate}}</span>
              </p>
              <p>
                <span>
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="map-marker-alt"/>
                </span>
                <span>{{this.siteData[this.place.siteNum - 1].name}}</span>
                <select class="header-select" v-model="areaWeather" @change="selectarea($event)">
                  <option
                    v-for="(site, index, w_key) in selectSite"
                    :value="site.id"
                    :key="w_key"
                  >{{site.name}}</option>
                </select>
              </p>
              <p>
                <span>{{weatherData.temperature}} </span>
                <span v-if="weatherData.desc === '' ">
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="cloud"/>
                </span>
                <span v-else-if="weatherData.desc === '' ">
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="cloud" style="color:#e4e4e4"/>
                </span>
                <span v-else-if="weatherData.desc === '' ">
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="sun" />
                </span>
                <span v-else-if="weatherData.desc === '' ">
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="cloud-showers-heavy" />
                </span>
                <span v-else-if="weatherData.desc === '' ">
                  <font-awesome-icon class="mt-3 pr-2" size="1x" icon="cloud-showers-heavy" />
                </span>
                <span v-else>
                    {{weatherData.desc}}
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- main -->
    <!-- <Main :showData="showData" :siteData="siteData" :place="place" :weatherData="weatherData"/> -->
  </div>
</template>

<script>
import Swiper from "@/components/Swiper/index.vue";
// import Main from "@/components/Main/index.vue";

let Today = new Date();

export default {
  name: "home",
  components: {
    Swiper,
    // Main
  },
  data() {
    return {};
  },
  computed: {
    nowYear() {
      return Today.getFullYear();
    },
    nowMounth() {
      return Today.getMonth() + 1;
    },
    nowDate() {
      return Today.getDate();
    },
    nowDay() {
      var day_list = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
      return day_list[Today.getDay()];
    },
    selectSite() {
      return this.siteData[this.place.siteNum - 1].towns;
    },
    areaWeather: {
      get() {
        return this.place.areaNum
      },
      set(val) {
        let areaTowns = this.siteData[this.place.siteNum - 1].towns;
        let Home_areaName = "";
        areaTowns.filter(item => {
          if (val === item.id) {
            return (Home_areaName = item.name);
          }
        });
        this.$emit("update:place", {
          siteNum: Number(this.siteData[this.place.siteNum - 1].id),
          siteName: String(this.siteData[this.place.siteNum - 1].name),
          areaNum: Number(val),
          areaName: String(Home_areaName)
        });
      }
    },
    dataWeather: {
      get() {
        return this.weatherData;
      },
      set(val) {
        this.$emit("update:weatherData", val);
      }
    }
  },
  props: {
    showData: {
      type: Array,
      required: true
    },
    siteData: {
      type: Array,
      required: true
    },
    place: {
      type: Object,
      required: true
    },
    weatherData: {
      type: Object,
      required: true
    }
  },
  methods: {
    selectarea(event) {
      if (event.target.value === "") {
        this.areaWeather = this.siteData[this.place.siteNum - 1].towns[0].id;
      }

      let d = new Date().getTime();
      let weather_url = `https://works.ioa.tw/weather/api/weathers/${
        this.areaWeather
      }.json?_=${d}`;
      this.axios({
        method: "get",
        url: weather_url
      }).then(res => {
        (this.Loading = true), (this.dataWeather = res.data);
      });
    }
  },
  mounted() {
    let d = new Date().getTime();
    let weather_url = `https://works.ioa.tw/weather/api/weathers/${
      this.areaWeather
    }.json?_=${d}`;
    this.axios({
      method: "get",
      url: weather_url
    }).then(res => {
      (this.Loading = true), (this.dataWeather = res.data);
    });
  }
};
</script>
