<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name:'Swiper',
  data() {
    return {
      swiperOption: {
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
    showFilter(){
      let vm = this
      return this.showData.filter((item) =>{
        let showSite = vm.place.siteName
        let location = item.showInfo[0].location
        let taipei = /|/g
        let taichung = /|/g
        let tainan = /|/g

        if(showSite === ''){
            return location.search(taipei) != -1
        } else if (showSite === ''){
            return location.search(taichung) != -1
        } else if (showSite === ''){
            return location.search(tainan) != -1
        } else {
            return location.search(showSite) != -1
        }
      })
    }
  },
  mounted() {
    // current swiper instance
    // swiper
    // console.log("this is current swiper instance object", this.swiper);
    // this.swiper.slideTo(3, 1000, false);
  },
  components: {
    swiper,
    swiperSlide
  },
  props:{
    showData: {
      type: Array,
      required: true
    },
    siteData: {
      type: Array,
      required: true
    },
    place: {
      type: Object,
      required: true
    },
  }
};
</script>



